/*####photo-slider####*/
.col-slider-comment {
  width: 72.1%;
  height: auto;
  float: left;
  margin-top: -17px;
}

.one-picture-place {
  height: auto;
  background: green;
  border-radius: 5px;
  border: 1px solid #cccccc;
  background: white;
  padding: 7px;
  position: relative;
}

.b-photo-slider {
  margin: 0;
  padding: 0;
  text-align: center;
  height: 94%;
}

.info-text-message {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  margin: auto;
  width: 277px;
  height: 50px;
  bottom: 0;
  text-align: center;
  display: none;
}

.photo-item {
  background: #F4F4F4;
  height: 100%;
  width: 100%;
  margin: 0px auto;
  position: relative;
  vertical-align: middle;
  display: inline-block;
}

.item {
  height: 200px;
  text-align: center;
}

.img-slider {
  max-height: 100%;
  max-width: 100%;
  width: auto;
  height: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  margin: auto;
  left: 0;
  right: 0;
}

.control-slide {
  height: auto;
  width: 100%;
  top: 0;
  left: 0;
  position: absolute;
  font-size: 32px;
  color: white;
  text-shadow: black 0px 0px 1px;
  z-index: 1000;
  height: 100%;
}

.left-slide {
  left: -100%;
  transition: all 0.4s ease;
  opacity: 0;
  position: absolute;
}

.active-slide {
  transition: opacity 0.7s ease;
  z-index: 20;
  opacity: 1;
  position: relative;
}

.right-slide {
  right: -100%;
  transition: all 0.4s ease;
  opacity: 0;
  position: absolute;
}

.btn-prew {
  cursor: pointer;
  float: left;
  margin-left: 20px;
  color: white;
  height: 100%;
  position: relative;
  width: 50px;
}

.btn-prew span::before {
  content: "\f104";
  font-family: FontAwesome;
}

.btn-next span::before {
  content: "\f105";
  font-family: FontAwesome;
}

a.btn-next span:hover {
  opacity: 1;
}

a.btn-prew span:hover {
  opacity: 1;
}

a.btn-next {
  cursor: pointer;
  float: right;
  margin-right: 20px;
  color: white;
  height: 100%;
  position: relative;
  width: 50px;
}

a.btn-next span,
a.btn-prew span {
  width: 1em;
  text-align: center;
  display: block;
  height: 50px;
  width: 50px;
  margin: auto;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  color: #ffffff;
  opacity: 0.5;
  text-shadow: 1px 1px 1px #cccccc;
  font-size: 60px;
  transition: all 0.3s ease;
}

.wrap-slider {
  position: relative;
  overflow: hidden;
  height: 100%;
}

.b-informstion {
  height: 27px;
  width: 98%;
  margin-top: 12px;
  position: absolute;
  z-index: 10;
  opacity: 1;
  bottom: 3px;
  z-index: 100;
}

#like-whom-pole {
  width: 90px;
  display: block;
  text-align: left;
}

.mini-avatar {
  width: 20px;
  height: 20px;
  margin: 2px;
  display: inline-block;
}

.mini-avatar img {
  width: 100%;
}

.b-pretens {
  float: left;
  display: block;
  width: 45%;
