@font-face {
    font-family: 'MainFont';
    src: url(../fonts/pt-sans.ttf);
}
body {
    background: url(../img/fon.jpg);
}
html {
    background: url(../img/fon.jpg);
    font: 400 16px 'MainFont';
}
img {
    width: 100%;
}
input, textarea {
    font-size: 13px;
    line-height: 20px;
    transition: all ease 0.3s;
}
input:focus,
textarea:focus {
    border: 1px solid #81afd5;
}

